<template>
  <div id="mouseClick" :style="clickPosition">
    {{ clickDir.current }}
  </div>
</template>

<script>
export default {
  name: "MouseClick",
  props:['all'],
  data(){
    return{
      clickDir:{
        // all:['宇宙','无敌','可爱','静静'],
        all:this.all,
        current:'',
        index:0
      },
      clickPosition:{
        display:'none',
        top:'',
        left:''
      }
  }},
  methods:{
    mouseclick(e) {
      // console.log(e.clientX,e.clientY);
      let dir = this.clickDir;
      let position = this.clickPosition
      let animation =
      dir.current = dir.all[dir.index++];
      if (dir.index === dir.all.length) {
        dir.index = 0;
      }
      position.left = e.pageX + 'px';
      position.top = e.pageY - 16 + 'px';
      position.display = 'block';
      setTimeout(() => {
        position.display = 'none';
      }, 300)
    }
  }
}
</script>

<style scoped>
#mouseClick{
  position: absolute;
  z-index: 99999;
  cursor: default;
  animation: fade .3s;
}
@keyframes fade {
  0% {
    opacity: 1;
  }
  100% {
    color: yellow;
    /*opacity: 0;*/
    transform:translate(30%,-100%);
  }
}
</style>



