import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state:{
    flag:0
  },
  mutations:{
    originNav(state){
      state.flag = 0;
    },
    noNav(state){
      state.flag = 1;
    },
    memoryNav(state){
      state.flag = 2;
    },
    techNav(state){
      state.flag = 3;
    }
  },
  getters:{

  },
  actions:{

  },
  modules:{

  }
  })



