<template>
  <div id="app" @click="mouseclick" v-if="routerAlive">
<!--    <MouseClick ref="mmm" :style="$refs.mmm.clickposition">{{ $refs.mmm.clickdir.current }}</MouseClick>-->
    <MouseClick ref="mmm" :all="dirall" style="color: red" />
    <ToHome></ToHome>
    <Header></Header>
    <Container></Container>
  </div>
</template>

<script>
import MouseClick from "./components/MouseClick";
import Header from "./components/Header";
import ToHome from "./components/ToHome";
import Container from "./components/Container";

export default {
  name: 'App',
  data:function () {
    return {
      message: "我是App组件的message666666666666啊66666666666666666666666666666",
      dirall:['宇宙','无敌','机智','可爱','萌萌哒','静静'],
      routerAlive:true
      // clickdir:{
      //   all:['宇宙','无敌','可爱','静静'],
      //   current:'',
      //   index:0
      // },
      // clickposition:{
      //   display:'',
      //   top:'',
      //   left:''
      // }
    };
  },
  provide(){
    return {
      routerRefresh: this.routerRefresh
    };
  },
  methods:{
    routerRefresh() {
      this.routerAlive = false;
      this.$nextTick(() => {
        this.routerAlive = true;
      });
    },
    //鼠标点击特效
    mouseclick(e) {
      this.$refs.mmm.mouseclick(e);
    }
    // mouseclick(e){
    //   // console.log(e.clientX,e.clientY);
    //   let dir = this.clickdir;
    //   let position = this.clickposition
    //   dir.current = dir.all[dir.index++];
    //   if(dir.index === dir.all.length){
    //     dir.index = 0;
    //   }
    //   position.left = e.clientX - 16 + 'px';
    //   position.top = e.clientY - 25 + 'px';
    //   position.display = 'block';
    //   setTimeout(() => {
    //     position.display = 'none';
    //   },500)
    // }
  },
  components:{
    MouseClick,
    ToHome,
    Header,
    // Home,
    Container
  }
}
</script>

<style>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
ul li {
  list-style: none;
}
a {
  text-decoration: none;
}
input,button {
  border: 0;
  outline: none;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /*text-align: center;*/
  color: #2c3e50;
  /*padding-top: 60px;*/
}
.w {
  width: 76%;
  min-width: 800px;
  margin: 0 auto;
}
</style>



