import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
import axios from 'axios'

// 全局配置axios参数，用来访问Bomb后端云数据库里面存储的博客数据
axios.defaults.baseURL = 'https://api.bmob.cn/1/classes'
axios.defaults.headers.post['X-Bmob-Application-Id'] = '08429484b25831af64ea1be4d0849dde'
axios.defaults.headers.post['X-Bmob-REST-API-Key'] = '1089b58597d15f18af3bc12a74f2ebfb'
axios.defaults.headers.post['Content-Type'] = 'application/json'
axios.defaults.headers.get['X-Bmob-Application-Id'] = '08429484b25831af64ea1be4d0849dde'
axios.defaults.headers.get['X-Bmob-REST-API-Key'] = '1089b58597d15f18af3bc12a74f2ebfb'
// console.log(axios.defaults.headers)

Vue.config.productionTip = true

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  data:{
    msg:'hello6666'
  },
  mounted() {
    window.app = this;
  },
  render: h => h(App)
})



