<template>
  <div class="searchResults">
    <h2>搜索结果</h2>
    <div v-if="waiting">
      正在搜索，请稍候...
    </div>
    <div v-else>
      {{ searchTip }}
    </div>
    <ul v-if="techArticleDir.length">
      <li v-for="techArticle in techArticleDir">
        <div class="typeImg">
          <img :src="'../../static/'+techArticle.categories[0]+'.jpg'" alt="6666">
        </div>
        <div class="content">
          <Router-link :to="'/technology/'+techArticle.id">
            <h3 @click="techNav">{{ techArticle.title }}</h3>
          </Router-link>
          <div v-html="techArticle.content.slice(0,50)+'...'"></div>
          <div>
            <ul>
              <li>作者:{{ techArticle.author }}&nbsp;&nbsp;创建时间:{{techArticle.createdAt}}</li>
              <li>分类:{{ techArticle.categories.join('|') }}&nbsp;&nbsp;文章id:{{techArticle.id}}</li>
            </ul>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "SearchResults",
  data(){
    return {
      techArticleDir: [],
      searchTip: '',
      waiting: true
    }
  },
  inject:["routerRefresh"],
  beforeRouteUpdate(to, from, next) {
    this.routerRefresh(); //路由销毁重建方法
    next();
  },
  computed:{
    searchValue(){
      return this.$route.params.value;
    }
  },
  methods:{
    techNav(){
      this.$store.commit('techNav');
    }
  },
  mounted() {
    this.techArticleDir = [];
    axios.get('/TechArticles')
      .then((data) => {
        // console.log(data.data.results[0])
        data.data.results = data.data.results.filter((value) => {
          // console.log(this.searchValue)
          return value.title.match(this.searchValue);
        }).reverse();
        this.searchTip = '关于 “' + this.searchValue+'” 共搜索到' + data.data.results.length + '条结果。';
        setTimeout(() => {
          this.waiting = false;
        },300);
        for (data of data.data.results) {
          data.id = data.objectId;
          // console.log(data)
          this.techArticleDir.push(data)
        }
      })
  }
}
</script>

<style scoped>
.searchResults>h2 {
  margin-bottom: 10px;
  padding-left: 8px;
}
.searchResults>div {
  width: 100%;
  margin-bottom: 10px;
  padding: 10px;
  background-color: #f60;
  border-radius: 20px;
}
.searchResults ul li{
  width: 100%;
  padding: 20px;
  margin-bottom: 10px;
  background-color: pink;
  border-radius: 20px;
  overflow: hidden;
}
.searchResults ul li .typeImg {
  float: left;
  width: 120px;
  height: 120px;
  border-radius: 16px;
  overflow: hidden;
}
.searchResults ul li .typeImg img {
  width: 120px;
  height: 120px;
}
.searchResults ul li .content {
  float: left;
  width: 76%;
  padding-left: 10px;
  overflow: hidden;
}
.searchResults ul li .content div:nth-of-type(1) {
  min-height: 30px;
  overflow: hidden;
}
.searchResults ul li .content div:nth-of-type(1) >>> h3,
.searchResults ul li .content div:nth-of-type(1) >>> p {
  font-size: 14px;
}
.searchResults ul li .content div:nth-of-type(2) ul {
  margin: 6px 0 0 0;
}
.searchResults ul li .content div:nth-of-type(2) ul li {
  margin: 0;
  padding: 0;
  width: auto;
  height: auto;
  font-size: 12px;
  color: #666;
}
.searchResults ul li .content a {
  display: inline-block;
  padding-bottom: 10px;
}
</style>



