import Vue from 'vue'
import Router from 'vue-router'
// const HelloWorld = () => import('@/components/HelloWorld')
// // const App = () => import("../App")
const Home = () => import("../components/Home")
const Mood = () => import("../components/Mood")
const MemoryDirectory = () => import("../components/MemoryDirectory")
const Memory = () => import("../components/Memory")
const TechnologyDirectory = () => import("../components/TechnologyDirectory")
const Technology = () => import("../components/Technology")
const Books = () => import("../components/Books")
const BlogMessage = () => import("../components/BlogMessage")
const AboutMe = () => import("../components/AboutMe")
const SearchResults = () => import("../components/SearchResults")


// import Home from "../components/Home"
// import Mood from "../components/Mood"
// import MemoryDirectory from "../components/MemoryDirectory"
// import Memory from "../components/Memory"
// import TechnologyDirectory from "../components/TechnologyDirectory"
// import Technology from "../components/Technology"
// import Books from "../components/Books"
// import BlogMessage from "../components/BlogMessage"
// import AboutMe from "../components/AboutMe"
// import SearchResults from "../components/SearchResults"


Vue.use(Router)


export default new Router({
  routes: [
    {
      path: '*',
      redirect:'/home'
    },
    {
      path: '/',
      redirect: '/home'
    },
    // {
    //   path:'/',
    //   name: 'App',
    //   component: App
    // },
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
    {
      path: '/mood',
      name: 'Mood',
      component: Mood
    },
    {
      path: '/memorydirectory',
      name: 'MemoryDirectory',
      component: MemoryDirectory
    },
    {
      path: '/memory/:id',
      name: 'Memory',
      component: Memory
    },
    {
      path: '/techdir',
      name: 'TechnologyDirectory',
      component: TechnologyDirectory
    },
    {
      path: '/technology/:id',
      name: 'Technology',
      component: Technology
    },
    {
      path: '/books',
      name: 'Books',
      component: Books
    },
    {
      path: '/blogmessage',
      name: 'BlogMessage',
      component: BlogMessage
    },
    {
      path: '/aboutme',
      name: 'AboutMe',
      component: AboutMe
    },
    {
      path: '/searchresults/:value',
      name: 'SearchResults',
      component: SearchResults
    }
    // {
    //   path: '/',
    //   name: 'HelloWorld',
    //   component: HelloWorld
    // }
  ],
  // mode:"history"
})



