<template>
  <div class="header-container">
    <div class="header w">
      <div class="header-nav">
        <ul>
          <li v-for="item in navItems" @click="clickNav">
            <router-link :to="item.link">{{item.text}}</router-link>
          </li>
        </ul>
      </div>
      <div class="header-search">
        <input type="search" v-model="value" placeholder="搜索技术文章">
        <button @click="search">搜索</button>
      </div>
    </div>
  </div>
</template>

<script>
import SearchResults from "./SearchResults";
export default {
  name: "Header",
  data(){
    return {
      navItems:[
        {
          name:'Home',
          link:"/home",
          text:"首页"
        },
        {
          name:'Mood',
          link:"/mood",
          text:"心情随笔"
        },
        {
          name:'MemoryDirectory',
          link:"/memorydirectory",
          text:"韶华追忆"
        },
        {
          name:'TechnologyDirectory',
          link:"/techdir",
          text:"技术分享"
        },
        {
          name:'Books',
          link:"/books",
          text:"书屋"
        },
        {
          name:'BlogMessage',
          link:"/blogmessage",
          text:"blog留言"
        },
        {
          name:'AboutMe',
          link: "/aboutme",
          text: "关于我"
        }
      ],
      value:''
    }
  },
  methods:{
    clickNav(e){
      // console.log(e.target.parentNode.parentNode.children)
      for (let i=0;i<e.target.parentNode.parentNode.children.length;i++){
        e.target.parentNode.parentNode.children[i].style.backgroundColor = ''
      }
      e.target.parentNode.style.backgroundColor = 'yellow'
    },
    search(){
      let headerNav = document.getElementsByClassName("header-nav");
      // console.log(this.value)
      if (this.value != ''){
        this.$store.commit('originNav');
        this.$store.commit('noNav');
        this.$router.replace('/searchresults/'+this.value);
        this.value = '';
      }
    }
  },
  watch:{
    '$store.state.flag':{
      handler(newValue,oldValue){
        switch (newValue){
          case 1:{
            let headerNav = document.getElementsByClassName("header-nav");
            for (let i=0;i<headerNav[0].children[0].children.length;i++){
              headerNav[0].children[0].children[i].style.backgroundColor = '';
            }
            break;
          }
          case 2:{
            let headerNav = document.getElementsByClassName("header-nav");
            for (let i=0;i<headerNav[0].children[0].children.length;i++){
              headerNav[0].children[0].children[i].style.backgroundColor = '';
            }
            headerNav[0].children[0].children[2].style.backgroundColor = 'yellow'
            break;
          }
          case 3:{
            let headerNav = document.getElementsByClassName("header-nav");
            for (let i=0;i<headerNav[0].children[0].children.length;i++){
              headerNav[0].children[0].children[i].style.backgroundColor = '';
            }
            headerNav[0].children[0].children[3].style.backgroundColor = 'yellow'
            break;
          }
        }
      },
      deep:true
    }
  },
  mounted() {
    let headerNav = document.getElementsByClassName("header-nav");
    switch (this.$route.name){
      case(this.navItems[0].name):
        headerNav[0].children[0].children[0].style.backgroundColor = 'yellow';
        break;
      case(this.navItems[1].name):
        headerNav[0].children[0].children[1].style.backgroundColor = 'yellow';
        break;
      case(this.navItems[2].name):
        headerNav[0].children[0].children[2].style.backgroundColor = 'yellow';
        break;
      case(this.navItems[3].name):
        headerNav[0].children[0].children[3].style.backgroundColor = 'yellow';
        break;
      case(this.navItems[4].name):
        headerNav[0].children[0].children[4].style.backgroundColor = 'yellow';
        break;
      case(this.navItems[5].name):
        headerNav[0].children[0].children[5].style.backgroundColor = 'yellow';
        break;
      case(this.navItems[6].name):
        headerNav[0].children[0].children[6].style.backgroundColor = 'yellow';
        break;
      case('SearchResults'):
        for (let i=0;i<headerNav[0].children[0].children.length;i++){
          headerNav[0].children[0].children[i].style.backgroundColor = ''
        }
        break;
      case('Memory'):
        for (let i=0;i<headerNav[0].children[0].children.length;i++){
          headerNav[0].children[0].children[i].style.backgroundColor = ''
        }
        break;
      case('Technology'):
        for (let i=0;i<headerNav[0].children[0].children.length;i++){
          headerNav[0].children[0].children[i].style.backgroundColor = ''
        }
        break;
      default:
        headerNav[0].children[0].children[0].style.backgroundColor = 'yellow';
        break;
    }
  }
}
</script>

<style scoped>
.header-container {
  position: sticky;
  top: 0;
  width: 100%;
  min-width: 800px;
  background-color: #b5bce8;
  opacity: 1;
  z-index: 66;
}
.header {
  display: flex;
  justify-content: space-between;
  height: 60px;
  min-width: 800px;
  font-size: 20px;
  line-height: 60px;
}
.header .header-nav ul {
  padding-left: 16px;
}
.header .header-nav ul li {
  float: left;
  padding: 0 8px;
  border-radius: 8px;
  transition: all .3s;
}
.header .header-nav ul li a {
  display: inline-block;
  height: 100%;
  color: #60f;
}
.header .header-search {
  position: relative;
  width: 24%;
  min-width: 180px;
  height: 32px;
  top: 12px;
  background-color: #fff;
  border: 2px solid pink;
  border-radius: 6px;
  overflow: hidden;
}
.header .header-search input,.header .header-search button {
  float: left;
  height: 28px;
  font-size: 16px;
  color: #333;
}
.header .header-search input {
  width: 80%;
  padding-left: 5px;
}
.header .header-search button {
  padding: 1px;
  width: 20%;
  background-color: #eee;
  border-left: none;
}
</style>



