<template>
  <div class="toHome" @mouseenter="mouseOver" @mouseleave="mouseOut">
    <!--    轮播图效果-->
    <div class="banner">
      <img :src="images[images.length-1]" alt="" title="+++++++++">
      <img v-for="img in images" :src="img" alt="" title="">
      <img :src="images[0]" alt="" title="--------">
    </div>
    <div class="left" @click="prevPage"><</div>
    <div class="right" @click="nextPage">></div>
<!--    <div class="points">-->
      <ul class="points">
        <li v-for="(item,index) in images" @click="jump(index)"></li>
      </ul>
<!--    </div>-->
    <div class="w">
      <Router-link to="/home">ZHJ's Blog</Router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: "ToHome",
  data(){
    return {
      interval:null,
      imgIndex:1,
      images:['../../static/timg.jpg','../../static/timg2.jpg','../../static/timg3.jpg'],
      frameTime:0.3
    }
  },
  mounted() {
    // this.interval = setInterval(() => {
    //   return this.nextPage();
    // },5000)
    this.mouseOut();
    let points = document.getElementsByClassName('points');
    points[0].children[0].style.width = '30px';
    let banner = document.getElementsByClassName('banner');
    banner[0].style.width = (this.images.length + 2) * 100 + '%';
    // console.log(banner[0].style.width);
    for(let i=0;i<(this.images.length+2);i++){
      banner[0].children[i].style.width = 100 / (this.images.length + 2) + '%';
      // console.log(banner[0].children[i].style.width)
    }
  },
  beforeDestroy() {
    this.mouseOver();
  },
  methods:{
    prevPage(){
      this.imgIndex--;
      let banner = document.getElementsByClassName('banner');
      //     console.log(parseInt(banner[0].style.left))
      banner[0].style.transition = 'all ' + this.frameTime + 's';
      // console.log(this.imgIndex)
      // banner[0].style.left = (0 - this.imgIndex) * 100 + '%';
      this.jump(this.imgIndex - 1);
      if (this.imgIndex<1){
        setTimeout(() => {
          banner[0].style.transition = 'none';
          this.imgIndex = this.images.length;
          banner[0].style.left = (0 - this.imgIndex) * 100 + '%';
          // this.jump(this.imgIndex - 1);
          // console.log(this.imgIndex);
        },this.frameTime*1000)
      }
    },
    nextPage(){
      this.imgIndex++;
      let banner = document.getElementsByClassName('banner');
      //     console.log(parseInt(banner[0].style.left))
      banner[0].style.transition = 'all ' + this.frameTime + 's';
      // console.log(this.imgIndex)
      // banner[0].style.left = (0 - this.imgIndex) * 100 + '%';
      this.jump(this.imgIndex - 1);
      if (this.imgIndex>this.images.length){
        setTimeout(() => {
          banner[0].style.transition = 'none';
          this.imgIndex = 1;
          banner[0].style.left = '-100%';
          // this.jump(this.imgIndex - 1);
          // console.log(this.imgIndex);
        },this.frameTime*1000)
      }
    },
    mouseOver(){
      clearInterval(this.interval)
      this.interval = null;
    },
    mouseOut(){
      this.interval = setInterval(() => {
        return this.nextPage();
      },3000);
    },
    jump(index){
      // console.log(index);
      let points = document.getElementsByClassName('points');
      let banner = document.getElementsByClassName('banner');
      for (let i=0;i<points[0].children.length;i++){
        points[0].children[i].style.width = '10px';
      }
      if (index>=0&&index<points[0].children.length){
        points[0].children[index].style.width = '30px';
      }else if (index>=points[0].children.length){
        points[0].children[0].style.width = '30px';
      }else {
        points[0].children[points[0].children.length-1].style.width = '30px';
      }
      this.imgIndex = index + 1;
      banner[0].style.left = (0 - this.imgIndex) * 100 + '%';
    }
  }
}
</script>

<style scoped>
.toHome {
  position: relative;
  width: 100%;
  min-width: 800px;
  height: 180px;
  background-color: #336666;
  overflow: hidden;
}
.toHome .banner {
  position: absolute;
  /*width: 600%;*/
  height: 180px;
  top: 0;
  left: -100%;
  background-color: pink;
}
.toHome .banner img {
  float: left;
  /*width: calc(100%/6);*/
  height: 100%;
}
.toHome .left,
.toHome .right {
  position: absolute;
  width: 60px;
  height: 60px;
  top: 60px;
  font-size: 50px;
  font-weight: 700;
  text-align: center;
  line-height: 60px;
  background-color: transparent;
  z-index: 99;
  cursor: pointer;
}
.toHome .left {
  left: 0;
  user-select: none;
}
.toHome .right {
  right: 0;
  user-select: none;
}
.toHome .points {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 10px;
  z-index: 99;
  background-color: rgba(20,120,120,.3);
  border-radius: 8px;
}
.toHome .points {
  padding: 5px 0;
  overflow: hidden;
}
.toHome .points li{
  float: left;
  margin: 0 5px;
  width: 10px;
  height: 10px;
  background-color: pink;
  border-radius: 5px;
  transition: all .3s;
}
.toHome .w a {
  position: absolute;
  top: 80px;
  margin-left: 16px;
  font-size: 36px;
  color: #8bdede;
  z-index: 99;
}
</style>



