<template>
  <div class="home w">
    <div class="home-left">
      <transition name="fade" mode="out-in">
        <Router-view></Router-view>
      </transition>
    </div>
    <div class="home-right">
      <div class="communicate">
        <div><h3>关注我</h3></div>
        <ul>
          <li><a href="javascript:;">新浪微博</a></li>
          <li><a href="javascript:;">微信</a></li>
          <li><a href="javascript:;">QQ</a></li>
          <li><a href="javascript:;">知乎</a></li>
        </ul>
      </div>
      <div class="last-article">
        <div><h3>最新文章</h3></div>
        <ul>技术分享
          <li v-for="techArticle in techArticleDir" @click="techNav">
            <Router-link :to="'/technology/'+techArticle.id">{{ techArticle.title }}</Router-link>
          </li>
        </ul>
        <ul>韶华追忆
          <li v-for="memoryArticle in memoryArticleDir" @click="memoryNav">
            <Router-link :to="'/memory/'+memoryArticle.id">{{ memoryArticle.title }}</Router-link>
          </li>
        </ul>
      </div>
      <div class="friends">
        <div><h3>友情链接</h3></div>
        <ul>
          <li v-for="friend in friends"><a :href="friend.url">{{ friend.name }}</a></li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "Content",
  data(){
    return {
      friends:[
        {
          name:"新浪微博",
          url:"javascript:;"
        },
        {
          name:"微信",
          url:"javascript:;"
        },
        {
          name:"QQ",
          url:"javascript:;"
        },
        {
          name:"知乎",
          url:"javascript:;"
        },
        {
          name:"新浪微博",
          url:"javascript:;"
        },
        {
          name:"微信",
          url:"javascript:;"
        },
        {
          name:"QQ",
          url:"javascript:;"
        },
        {
          name:"知乎",
          url:"javascript:;"
        }
      ],
      techArticleDir:[],
      memoryArticleDir:[]
    }
  },
  inject:["routerRefresh"],
  beforeRouteUpdate(to, from, next) {
    this.routerRefresh(); //路由销毁重建方法
    next();
  },
  methods:{
    techNav() {
      this.$store.commit('originNav');
      this.$store.commit('techNav');
    },
    memoryNav() {
      this.$store.commit('originNav');
      this.$store.commit('memoryNav');
    }
  },
  mounted() {
    this.techArticleDir = [];
    this.memoryArticleDir = [];
    axios.get('/TechArticles')
      .then((data) => {
        // console.log(data.data.results[0])
        data.data.results = data.data.results.reverse().slice(0,3);
        for (data of data.data.results) {
          data.id = data.objectId;
          // console.log(data)
          this.techArticleDir.push(data);
        }
      });
    axios.get('/MemoryArticles')
      .then((data) =>{
        // console.log(data.data.results[0])
        data.data.results = data.data.results.reverse().slice(0,3);
        for (data of data.data.results){
          data.id = data.objectId;
          // console.log(data)
          this.memoryArticleDir.push(data);
        }
      });
  }
}
</script>

<style scoped>
.home {
  display: flex;
}
.home-left {
  flex: 3;
  align-self: flex-start;
  padding: 30px 30px 30px 15px;
  overflow-wrap: break-word;
  overflow: hidden;
}
.fade-enter {
  opacity: 0;
}
.fade-leave {
  opacity: 1;
}
.fade-enter-active {
  transition: opacity .2s;
}
.fade-leave-active {
  opacity: 0;
  transition: opacity .2s;
}
.home .home-right {
  flex: 1;
  align-self: flex-start;
  padding: 24px 0 0 5px;
}
.home .home-right .communicate {
  padding: 0 0 10px 0;
}
.home .home-right .communicate div:nth-child(1)
,.home .home-right .last-article div:nth-child(1)
,.home .home-right .friends div:nth-child(1){
  height: 50px;
  line-height: 50px;
  border-bottom: 3px solid #666;
}
.home .home-right .communicate ul {
  overflow: hidden;
}
.home .home-right .communicate ul li {
  float: left;;
}
.home .home-right .communicate ul li a {
  display: inline-block;
  width: 64px;
  height: 64px;
  margin-bottom: 10px;
  font-size: 14px;
  text-align: center;
  line-height: 70px;
  background-color: yellow;
}
.home .home-right .last-article ul {
  padding: 10px 0 10px 0;
}
.home .home-right .last-article ul li {
  position: relative;
  list-style: disc;
  width: 95%;
  left: 12px;
  line-height: 18px;
  font-size: 12px;
  text-overflow: ellipsis;
}
.home .home-right .friends ul li {
  float: left;
  padding: 5px;
  font-size: 12px;
  line-height: 30px;
}
</style>



